import React from 'react';
import { Notaire, NotaireStatut } from '../types';

interface Props {
  notaires: Notaire[];
  onNotaireClick: (notaire: Notaire) => void;
  onStatutChange: (notaire: Notaire, statut: NotaireStatut) => void;
}

const StatutBadge: React.FC<{ statut: NotaireStatut }> = ({ statut }) => {
  const styles = {
    favori: 'bg-yellow-100 text-yellow-800',
    envisage: 'bg-blue-100 text-blue-800',
    non_interesse: 'bg-gray-100 text-gray-800',
    non_defini: 'bg-gray-50 text-gray-600'
  };

  const labels = {
    favori: '⭐ Favori',
    envisage: '🤔 À envisager',
    non_interesse: '❌ Non intéressé',
    non_defini: 'Non défini'
  };

  return (
    <span className={`px-3 py-1 inline-flex text-xs font-medium rounded-full ${styles[statut]}`}>
      {labels[statut]}
    </span>
  );
};

const NotairesTable: React.FC<Props> = ({ notaires, onNotaireClick, onStatutChange }) => {
  return (
    <div className="bg-white rounded-xl shadow-xl overflow-hidden">
      <div className="overflow-x-auto">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Office Notarial
              </th>
              <th className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Localisation
              </th>
              <th className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Équipe
              </th>
              <th className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Services
              </th>
              <th className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Statut
              </th>
              <th className="px-6 py-4 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {notaires.map((notaire) => (
              <tr
                key={notaire.id}
                className="hover:bg-indigo-50 transition-colors duration-200"
              >
                <td className="px-6 py-4">
                  <div className="text-sm font-medium text-gray-900 mb-1">
                    {notaire.officeNotarial}
                  </div>
                  <div className="text-sm text-gray-500">{notaire.adresse}</div>
                </td>
                <td className="px-6 py-4">
                  <div className="text-sm font-medium text-gray-900 mb-1">
                    {notaire.ville}
                  </div>
                  <div className="text-sm text-gray-500">
                    {notaire.codePostal} - {notaire.departement}
                  </div>
                </td>
                <td className="px-6 py-4">
                  <div className="flex flex-col space-y-2">
                    <div className="flex items-center space-x-2">
                      <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-indigo-100 text-indigo-800">
                        {notaire.nbAssocies} associé{notaire.nbAssocies > 1 ? 's' : ''}
                      </span>
                      <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-purple-100 text-purple-800">
                        {notaire.nbSalaries} salarié{notaire.nbSalaries > 1 ? 's' : ''}
                      </span>
                    </div>
                    {notaire.notairesAssocies && (
                      <div className="text-xs text-gray-500">
                        Associés : {notaire.notairesAssocies}
                      </div>
                    )}
                  </div>
                </td>
                <td className="px-6 py-4">
                  <div className="flex flex-col space-y-2">
                    <span
                      className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                        notaire.serviceNego
                          ? 'bg-green-100 text-green-800'
                          : 'bg-gray-100 text-gray-800'
                      }`}
                    >
                      {notaire.serviceNego ? '✓ Service négociation' : '✗ Pas de négociation'}
                    </span>
                    {notaire.email && (
                      <a
                        href={`mailto:${notaire.email}`}
                        className="text-xs text-indigo-600 hover:text-indigo-900"
                      >
                        {notaire.email}
                      </a>
                    )}
                  </div>
                </td>
                <td className="px-6 py-4">
                  <div className="flex flex-col space-y-2">
                    <StatutBadge statut={notaire.statut} />
                    <select
                      value={notaire.statut}
                      onChange={(e) => onStatutChange(notaire, e.target.value as NotaireStatut)}
                      className="text-xs border-gray-300 rounded-md shadow-sm focus:border-indigo-500 focus:ring-indigo-500"
                    >
                      <option value="non_defini">Non défini</option>
                      <option value="favori">⭐ Favori</option>
                      <option value="envisage">🤔 À envisager</option>
                      <option value="non_interesse">❌ Non intéressé</option>
                    </select>
                  </div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm">
                  <button
                    onClick={() => onNotaireClick(notaire)}
                    className="text-indigo-600 hover:text-indigo-900 font-medium transition-colors duration-200"
                  >
                    Voir détails →
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default NotairesTable; 