// Service pour le géocodage des adresses
import { GeocodingResult, Notaire } from '../types';
import { googleSheetsService } from './googleSheets';
import axios from 'axios';

const DELAY_BETWEEN_REQUESTS = 1000; // 1 seconde
const MAX_RETRIES = 3;
const CACHE_KEY = 'geocoding_cache';
const CACHE_EXPIRY = 30 * 24 * 60 * 60 * 1000; // 30 days in milliseconds
const GEOCODING_CACHE_KEY = 'geocoding_results';
const SAVE_BATCH_SIZE = 20; // Sauvegarder tous les 20 notaires

interface CacheEntry {
  result: GeocodingResult;
  timestamp: number;
}

interface GeocodingCache {
  [address: string]: CacheEntry;
}

const getCache = (): GeocodingCache => {
  try {
    const cache = localStorage.getItem(CACHE_KEY);
    return cache ? JSON.parse(cache) : {};
  } catch {
    return {};
  }
};

const setCache = (cache: GeocodingCache) => {
  try {
    localStorage.setItem(CACHE_KEY, JSON.stringify(cache));
  } catch (error) {
    console.warn('Failed to save geocoding cache:', error);
  }
};

const getCachedResult = (address: string): GeocodingResult | null => {
  const cache = getCache();
  const entry = cache[address];
  
  if (entry && Date.now() - entry.timestamp < CACHE_EXPIRY) {
    return entry.result;
  }
  
  return null;
};

const setCachedResult = (address: string, result: GeocodingResult) => {
  const cache = getCache();
  cache[address] = {
    result,
    timestamp: Date.now()
  };
  setCache(cache);
};

const sleep = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

export const geocodeAddress = async (address: string, notaire?: Notaire, retryCount = 0): Promise<GeocodingResult> => {
  try {
    if (!address.trim()) {
      throw new Error('Adresse vide');
    }

    // Check cache first
    const cachedResult = getCachedResult(address);
    if (cachedResult) {
      console.log(`Using cached result for ${address}`);
      return cachedResult;
    }

    const response = await axios.get(
      `https://api-adresse.data.gouv.fr/search/?q=${encodeURIComponent(address)}&limit=1`,
      {
        headers: {
          'Accept': 'application/json'
        },
        timeout: 10000
      }
    );

    if (response.data && response.data.features && response.data.features.length > 0) {
      const feature = response.data.features[0];
      const [lon, lat] = feature.geometry.coordinates;
      const geocodingResult = {
        lat,
        lon,
        display_name: feature.properties.label,
        score: feature.properties.score
      };

      // Cache the successful result
      setCachedResult(address, geocodingResult);

      // Si un notaire est fourni, mettre à jour son historique
      if (notaire) {
        const historyEntry = {
          date: new Date().toISOString(),
          address,
          success: true,
          coordinates: {
            lat: geocodingResult.lat,
            lon: geocodingResult.lon
          },
          score: geocodingResult.score
        };

        notaire.geocodingHistory = [
          ...(notaire.geocodingHistory || []),
          historyEntry
        ];

        // Mettre à jour le score de géocodage
        notaire.geoScore = geocodingResult.score;
        // Mettre à jour la date de modification
        notaire.dateModification = new Date().toISOString();
      }

      return geocodingResult;
    }
    
    console.warn(`Adresse non trouvée: ${address}`);
    const errorResult = {
      lat: 0,
      lon: 0,
      display_name: '',
      error: 'Adresse non trouvée'
    };

    // Enregistrer l'échec dans l'historique
    if (notaire) {
      const historyEntry = {
        date: new Date().toISOString(),
        address,
        success: false,
        error: 'Adresse non trouvée'
      };

      notaire.geocodingHistory = [
        ...(notaire.geocodingHistory || []),
        historyEntry
      ];

      // Mettre à jour la date de modification même en cas d'échec
      notaire.dateModification = new Date().toISOString();
    }

    return errorResult;
  } catch (error) {
    console.error(`Erreur lors du géocodage de l'adresse ${address}:`, error);
    
    // Retry on network errors
    if (retryCount < MAX_RETRIES && axios.isAxiosError(error) && (error.code === 'ERR_NETWORK' || error.response?.status === 429)) {
      console.log(`Tentative ${retryCount + 1}/${MAX_RETRIES} pour ${address}`);
      await sleep(DELAY_BETWEEN_REQUESTS * (retryCount + 1));
      return geocodeAddress(address, notaire, retryCount + 1);
    }

    const errorResult = {
      lat: 0,
      lon: 0,
      display_name: '',
      error: error instanceof Error ? error.message : 'Erreur lors du géocodage'
    };

    // Enregistrer l'erreur dans l'historique
    if (notaire) {
      const historyEntry = {
        date: new Date().toISOString(),
        address,
        success: false,
        error: error instanceof Error ? error.message : 'Erreur inconnue'
      };

      notaire.geocodingHistory = [
        ...(notaire.geocodingHistory || []),
        historyEntry
      ];

      // Mettre à jour la date de modification même en cas d'erreur
      notaire.dateModification = new Date().toISOString();
    }

    return errorResult;
  }
};

let isProcessing = false;

// Fonction pour géocoder un seul notaire (utilisée lors de la modification d'adresse)
export async function geocodeSingleNotaire(notaire: Notaire): Promise<Notaire> {
  try {
    const fullAddress = `${notaire.adresse}, ${notaire.codePostal} ${notaire.ville}`;
    console.log(`Géocodage individuel pour ${notaire.officeNotarial}...`);
    
    const coords = await geocodeAddress(fullAddress, notaire);
    
    if (coords && !coords.error) {
      return {
        ...notaire,
        latitude: coords.lat,
        longitude: coords.lon,
        display_name: coords.display_name,
        geoStatus: 'success',
        geoScore: coords.score,
        dateModification: new Date().toISOString()
      };
    } else {
      return {
        ...notaire,
        geoStatus: 'error',
        dateModification: new Date().toISOString()
      };
    }
  } catch (error) {
    console.error(`Erreur lors du géocodage de ${notaire.officeNotarial}:`, error);
    return {
      ...notaire,
      geoStatus: 'error',
      dateModification: new Date().toISOString()
    };
  }
}

// Fonction pour sauvegarder localement les résultats
const saveToLocalStorage = (notaires: Notaire[]) => {
  try {
    localStorage.setItem(GEOCODING_CACHE_KEY, JSON.stringify(notaires));
  } catch (error) {
    console.warn('Erreur lors de la sauvegarde locale:', error);
  }
};

// Fonction pour charger les résultats locaux
const loadFromLocalStorage = (): Notaire[] => {
  try {
    const saved = localStorage.getItem(GEOCODING_CACHE_KEY);
    return saved ? JSON.parse(saved) : [];
  } catch (error) {
    console.warn('Erreur lors du chargement local:', error);
    return [];
  }
};

// Fonction pour géocoder une liste de notaires
export async function geocodeBatch(
  notaires: Notaire[],
  onProgress?: (updatedNotaire: Notaire) => void
): Promise<Notaire[]> {
  if (isProcessing) {
    console.log('Un géocodage est déjà en cours, opération ignorée');
    return notaires;
  }

  try {
    isProcessing = true;

    // Charger d'abord les données existantes de Google Sheets
    let existingData: Notaire[] = [];
    try {
      const response = await googleSheetsService.loadFromSheet();
      existingData = response.notaires;
      console.log(`${existingData.length} notaires chargés depuis Google Sheets`);
    } catch (error) {
      console.error('Erreur lors du chargement des données existantes:', error);
      // En cas d'erreur, essayer de charger depuis le localStorage
      existingData = loadFromLocalStorage();
      console.log(`${existingData.length} notaires chargés depuis le localStorage`);
    }

    // Créer une map des données existantes
    const existingDataMap = new Map(existingData.map(n => [n.id, n]));

    const results = [...notaires];
    let modifiedCount = 0;
    const notairesAGeocoder = notaires.filter(n => !n.latitude || !n.longitude);

    console.log(`Début du géocodage pour ${notairesAGeocoder.length} notaires...`);

    for (let i = 0; i < notaires.length; i++) {
      const notaire = notaires[i];
      
      // Vérifier si le notaire existe déjà avec des coordonnées
      const existingNotaire = existingDataMap.get(notaire.id);
      if (existingNotaire?.latitude && existingNotaire?.longitude) {
        console.log(`Utilisation des coordonnées existantes pour ${notaire.officeNotarial}`);
        results[i] = {
          ...notaire,
          latitude: existingNotaire.latitude,
          longitude: existingNotaire.longitude,
          display_name: existingNotaire.display_name,
          geoStatus: existingNotaire.geoStatus,
          geoScore: existingNotaire.geoScore,
          geocodingHistory: existingNotaire.geocodingHistory
        };
        onProgress?.(results[i]);
        continue;
      }
      
      // Si déjà géocodé avec succès dans la session actuelle, passer au suivant
      if (notaire.latitude && notaire.longitude) {
        continue;
      }

      try {
        console.log(`Géocodage de ${i + 1}/${notaires.length}: ${notaire.officeNotarial}`);
        
        let coords: GeocodingResult | null = null;
        
        // Essayer d'abord avec l'adresse complète
        if (notaire.adresse && notaire.adresse.trim() !== '') {
          const fullAddress = `${notaire.adresse}, ${notaire.codePostal} ${notaire.ville}`;
          coords = await geocodeAddress(fullAddress, notaire);
          await sleep(DELAY_BETWEEN_REQUESTS);
        }
        
        // Si pas de résultat ou score faible, essayer avec la ville et le code postal
        if (!coords || coords.error || (coords.score && coords.score < 0.5)) {
          const cityAddress = `${notaire.codePostal} ${notaire.ville}`;
          coords = await geocodeAddress(cityAddress, notaire);
          await sleep(DELAY_BETWEEN_REQUESTS);
        }

        if (coords && !coords.error) {
          const updatedNotaire: Notaire = {
            ...notaire,
            latitude: coords.lat,
            longitude: coords.lon,
            display_name: coords.display_name,
            geoStatus: 'success' as const,
            geoScore: coords.score,
            dateModification: new Date().toISOString()
          };
          results[i] = updatedNotaire;
          modifiedCount++;
          
          // Mettre à jour la map des données existantes
          existingDataMap.set(updatedNotaire.id, updatedNotaire);
          
          // Notifier le composant de la mise à jour
          onProgress?.(updatedNotaire);
        } else {
          const updatedNotaire: Notaire = {
            ...notaire,
            geoStatus: 'error' as const,
            display_name: coords?.display_name,
            latitude: coords?.lat,
            longitude: coords?.lon,
            geoScore: coords?.score,
            dateModification: new Date().toISOString()
          };
          results[i] = updatedNotaire;
          
          // Mettre à jour la map des données existantes
          existingDataMap.set(updatedNotaire.id, updatedNotaire);
          
          // Notifier même en cas d'erreur
          onProgress?.(updatedNotaire);
        }

        // Sauvegarder par lots
        if (modifiedCount > 0 && modifiedCount % SAVE_BATCH_SIZE === 0) {
          console.log(`Sauvegarde intermédiaire après ${modifiedCount} modifications...`);
          const allNotaires = Array.from(existingDataMap.values());
          saveToLocalStorage(allNotaires);
          // La synchronisation avec Google Sheets sera gérée par le composant parent
        }

      } catch (error) {
        console.error(`Erreur lors du géocodage de ${notaire.officeNotarial}:`, error);
        const updatedNotaire: Notaire = {
          ...notaire,
          geoStatus: 'error' as const,
          dateModification: new Date().toISOString()
        };
        results[i] = updatedNotaire;
        
        // Mettre à jour la map des données existantes
        existingDataMap.set(updatedNotaire.id, updatedNotaire);
        
        // Notifier même en cas d'erreur
        onProgress?.(updatedNotaire);
      }
    }

    // Sauvegarde finale
    console.log(`Sauvegarde finale de ${existingDataMap.size} notaires...`);
    const allNotaires = Array.from(existingDataMap.values());
    saveToLocalStorage(allNotaires);
    // La synchronisation avec Google Sheets sera gérée par le composant parent

    return results;
  } finally {
    isProcessing = false;
  }
}

export const GeocodingService = {
  geocodeAddress,
  geocodeBatch
}; 