import React, { useState, useEffect, useRef, Fragment } from 'react';
import { Notaire, NotaireStatut } from '../types';
import { geocodeSingleNotaire } from '../services/geocoding';
import ContactHistory from './ContactHistory';
import { Dialog, Transition, Listbox } from '@headlessui/react';
import { CheckIcon, ChevronUpDownIcon } from '@heroicons/react/24/solid';

interface Props {
  isOpen: boolean;
  onClose: () => void;
  notaire: Notaire;
  onSave: (notaire: Notaire) => void;
  isEditing: boolean;
  setIsEditing: (isEditing: boolean) => void;
}

const statusOptions = [
  { value: 'non_defini', label: 'Non défini', icon: '❓' },
  { value: 'favori', label: 'Favori', icon: '⭐' },
  { value: 'envisage', label: 'À envisager', icon: '🤔' },
  { value: 'non_interesse', label: 'Non intéressé', icon: '❌' }
] as const;

type Option = {
  value: NotaireStatut;
  label: string;
  icon?: string;
};

const CustomSelect: React.FC<{
  value: NotaireStatut;
  onChange: (value: NotaireStatut) => void;
  options: readonly Option[];
  label: string;
}> = ({ value, onChange, options, label }) => {
  return (
    <Listbox value={value} onChange={onChange}>
      <div className="relative mt-1">
        <Listbox.Label className="block text-sm font-medium text-gray-700 mb-1">
          {label}
        </Listbox.Label>
        <Listbox.Button className="relative w-full cursor-default rounded-lg bg-white py-2 pl-3 pr-10 text-left shadow-sm border border-gray-300 focus:outline-none focus:ring-2 focus:ring-teal-500 focus:border-teal-500 sm:text-sm">
          <span className="flex items-center">
            {options.find(opt => opt.value === value)?.icon && (
              <span className="mr-2">{options.find(opt => opt.value === value)?.icon}</span>
            )}
            <span className="block truncate">{options.find(opt => opt.value === value)?.label}</span>
          </span>
          <span className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-2">
            <ChevronUpDownIcon className="h-5 w-5 text-gray-400" aria-hidden="true" />
          </span>
        </Listbox.Button>
        <Transition
          as={Fragment}
          leave="transition ease-in duration-100"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <Listbox.Options className="absolute z-10 mt-1 max-h-60 w-full overflow-auto rounded-md bg-white py-1 text-base shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none sm:text-sm">
            {options.map((option) => (
              <Listbox.Option
                key={option.value}
                className={({ active }: { active: boolean }) =>
                  `relative cursor-default select-none py-2 pl-10 pr-4 ${
                    active ? 'bg-teal-100 text-teal-900' : 'text-gray-900'
                  }`
                }
                value={option.value}
              >
                {({ selected }: { selected: boolean }) => (
                  <>
                    <span className={`flex items-center ${selected ? 'font-medium' : 'font-normal'}`}>
                      {option.icon && <span className="mr-2">{option.icon}</span>}
                      {option.label}
                    </span>
                    {selected ? (
                      <span className="absolute inset-y-0 left-0 flex items-center pl-3 text-teal-600">
                        <CheckIcon className="h-5 w-5" aria-hidden="true" />
                      </span>
                    ) : null}
                  </>
                )}
              </Listbox.Option>
            ))}
          </Listbox.Options>
        </Transition>
      </div>
    </Listbox>
  );
};

const NotaireModal: React.FC<Props> = ({ isOpen, onClose, notaire, onSave, isEditing, setIsEditing }) => {
  const [editedNotaire, setEditedNotaire] = useState<Notaire>(notaire);
  const [activeTab, setActiveTab] = useState<'info' | 'contacts'>('info');
  const [geocodingStatus, setGeocodingStatus] = useState<string>('');
  const [isLoadingAdresse, setIsLoadingAdresse] = useState(false);

  useEffect(() => {
    setEditedNotaire(notaire);
  }, [notaire]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setEditedNotaire(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleCheckboxChange = (name: string, checked: boolean) => {
    setEditedNotaire(prev => ({
      ...prev,
      [name]: checked
    }));
  };

  const handleStatusChange = (value: NotaireStatut) => {
    setEditedNotaire(prev => ({
      ...prev,
      statut: value
    }));
  };

  const handleSave = async () => {
    if (
      editedNotaire.adresse !== notaire.adresse ||
      editedNotaire.codePostal !== notaire.codePostal ||
      editedNotaire.ville !== notaire.ville
    ) {
      setIsLoadingAdresse(true);
      setGeocodingStatus('Géocodage de la nouvelle adresse...');
      try {
        const updatedNotaire = await geocodeSingleNotaire(editedNotaire);
        if (updatedNotaire.geoStatus === 'success') {
          setGeocodingStatus('Adresse géocodée avec succès');
          onSave(updatedNotaire);
        } else {
          setGeocodingStatus('Erreur lors du géocodage de l\'adresse');
          onSave(editedNotaire); // Sauvegarder quand même les modifications
        }
      } catch (error) {
        console.error('Erreur lors du géocodage:', error);
        setGeocodingStatus('Erreur lors du géocodage de l\'adresse');
        onSave(editedNotaire); // Sauvegarder quand même les modifications
      } finally {
        setIsLoadingAdresse(false);
      }
    } else {
      onSave(editedNotaire);
    }
    setIsEditing(false);
  };

  const handleCancel = () => {
    setEditedNotaire(notaire);
    setIsEditing(false);
  };

  return (
    <Transition.Root show={isOpen} as={Fragment}>
      <Dialog as="div" className="relative z-50" onClose={onClose}>
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
        </Transition.Child>

        <div className="fixed inset-0 z-10 overflow-y-auto">
          <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
              enterTo="opacity-100 translate-y-0 sm:scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 translate-y-0 sm:scale-100"
              leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
            >
              <Dialog.Panel className="relative transform overflow-hidden rounded-lg bg-white text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-4xl">
                <div className="bg-white">
                  <div className="p-6 border-b border-gray-200">
                    <div className="flex items-center justify-between">
                      <Dialog.Title as="h3" className="text-2xl font-bold text-gray-900">
                        {notaire.officeNotarial}
                      </Dialog.Title>
                      <div className="flex items-center space-x-4">
                        {!isEditing && (
                          <button
                            onClick={() => setIsEditing(true)}
                            className="px-4 py-2 text-sm font-medium rounded-lg bg-teal-100 text-teal-800 hover:bg-teal-200 transition-colors duration-200"
                          >
                            Modifier
                          </button>
                        )}
                        <button
                          onClick={onClose}
                          className="text-gray-400 hover:text-gray-500"
                        >
                          <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                          </svg>
                        </button>
                      </div>
                    </div>
                    <div className="mt-4 flex space-x-4">
                      <button
                        onClick={() => setActiveTab('info')}
                        className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors duration-200 ${
                          activeTab === 'info'
                            ? 'bg-teal-100 text-teal-800'
                            : 'text-gray-600 hover:text-gray-800 hover:bg-gray-100'
                        }`}
                      >
                        Informations
                      </button>
                      <button
                        onClick={() => setActiveTab('contacts')}
                        className={`px-4 py-2 text-sm font-medium rounded-lg transition-colors duration-200 ${
                          activeTab === 'contacts'
                            ? 'bg-teal-100 text-teal-800'
                            : 'text-gray-600 hover:text-gray-800 hover:bg-gray-100'
                        }`}
                      >
                        Contacts et suivi
                      </button>
                    </div>
                  </div>

                  <div className="p-6">
                    {activeTab === 'info' ? (
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <div className="space-y-6">
                          <div>
                            <h3 className="text-lg font-medium text-gray-900 mb-4">
                              Informations générales
                            </h3>
                            <div className="space-y-4">
                              {isEditing ? (
                                <CustomSelect
                                  value={editedNotaire.statut}
                                  onChange={handleStatusChange}
                                  options={statusOptions}
                                  label="Statut"
                                />
                              ) : (
                                <div className="mb-4">
                                  <label className="block text-sm font-medium text-gray-700 mb-1">
                                    Statut
                                  </label>
                                  <div className="flex items-center space-x-2">
                                    <span className="text-lg">
                                      {statusOptions.find(opt => opt.value === notaire.statut)?.icon}
                                    </span>
                                    <span className="text-gray-900">
                                      {statusOptions.find(opt => opt.value === notaire.statut)?.label}
                                    </span>
                                  </div>
                                </div>
                              )}

                              <div className="relative">
                                <label className="block text-sm font-medium text-gray-700">
                                  Adresse
                                </label>
                                {isEditing ? (
                                  <input
                                    type="text"
                                    name="adresse"
                                    value={editedNotaire.adresse}
                                    onChange={handleInputChange}
                                    className="mt-1 block w-full rounded-lg border-gray-300 shadow-sm focus:border-teal-500 focus:ring-teal-500"
                                  />
                                ) : (
                                  <div className="mt-1 text-gray-900">{notaire.adresse}</div>
                                )}
                              </div>

                              <div>
                                <label className="block text-sm font-medium text-gray-700">
                                  Code Postal
                                </label>
                                {isEditing ? (
                                  <input
                                    type="text"
                                    name="codePostal"
                                    value={editedNotaire.codePostal}
                                    onChange={handleInputChange}
                                    className="mt-1 block w-full rounded-lg border-gray-300 shadow-sm focus:border-teal-500 focus:ring-teal-500"
                                  />
                                ) : (
                                  <div className="mt-1 text-gray-900">{notaire.codePostal}</div>
                                )}
                              </div>

                              <div>
                                <label className="block text-sm font-medium text-gray-700">
                                  Ville
                                </label>
                                {isEditing ? (
                                  <input
                                    type="text"
                                    name="ville"
                                    value={editedNotaire.ville}
                                    onChange={handleInputChange}
                                    className="mt-1 block w-full rounded-lg border-gray-300 shadow-sm focus:border-teal-500 focus:ring-teal-500"
                                  />
                                ) : (
                                  <div className="mt-1 text-gray-900">{notaire.ville}</div>
                                )}
                              </div>

                              <div>
                                <label className="block text-sm font-medium text-gray-700">
                                  Email
                                </label>
                                {isEditing ? (
                                  <input
                                    type="email"
                                    name="email"
                                    value={editedNotaire.email || ''}
                                    onChange={handleInputChange}
                                    className="mt-1 block w-full rounded-lg border-gray-300 shadow-sm focus:border-teal-500 focus:ring-teal-500"
                                  />
                                ) : (
                                  <div className="mt-1">
                                    {notaire.email ? (
                                      <a
                                        href={`mailto:${notaire.email}`}
                                        className="text-teal-600 hover:text-teal-800"
                                      >
                                        {notaire.email}
                                      </a>
                                    ) : (
                                      <span className="text-gray-500">Non renseigné</span>
                                    )}
                                  </div>
                                )}
                              </div>
                            </div>
                          </div>

                          <div>
                            <h3 className="text-lg font-medium text-gray-900 mb-4">
                              Équipe
                            </h3>
                            <div className="grid grid-cols-2 gap-4">
                              <div className="bg-teal-50 p-4 rounded-lg">
                                <div className="text-sm font-medium text-teal-800">
                                  Notaires associés
                                </div>
                                {isEditing ? (
                                  <>
                                    <input
                                      type="number"
                                      name="nbAssocies"
                                      value={editedNotaire.nbAssocies}
                                      onChange={handleInputChange}
                                      min="0"
                                      className="mt-1 block w-full rounded-lg border-gray-300 shadow-sm focus:border-teal-500 focus:ring-teal-500"
                                    />
                                    <input
                                      type="text"
                                      name="notairesAssocies"
                                      value={editedNotaire.notairesAssocies}
                                      onChange={handleInputChange}
                                      placeholder="Noms des associés"
                                      className="mt-2 block w-full rounded-lg border-gray-300 shadow-sm focus:border-teal-500 focus:ring-teal-500"
                                    />
                                  </>
                                ) : (
                                  <>
                                    <div className="mt-1 text-2xl font-bold text-teal-900">
                                      {notaire.nbAssocies}
                                    </div>
                                    {notaire.notairesAssocies && (
                                      <div className="mt-2 text-sm text-teal-600">
                                        {notaire.notairesAssocies}
                                      </div>
                                    )}
                                  </>
                                )}
                              </div>

                              <div className="bg-teal-50 p-4 rounded-lg">
                                <div className="text-sm font-medium text-teal-800">
                                  Notaires salariés
                                </div>
                                {isEditing ? (
                                  <>
                                    <input
                                      type="number"
                                      name="nbSalaries"
                                      value={editedNotaire.nbSalaries}
                                      onChange={handleInputChange}
                                      min="0"
                                      className="mt-1 block w-full rounded-lg border-gray-300 shadow-sm focus:border-teal-500 focus:ring-teal-500"
                                    />
                                    <input
                                      type="text"
                                      name="notairesSalaries"
                                      value={editedNotaire.notairesSalaries}
                                      onChange={handleInputChange}
                                      placeholder="Noms des salariés"
                                      className="mt-2 block w-full rounded-lg border-gray-300 shadow-sm focus:border-teal-500 focus:ring-teal-500"
                                    />
                                  </>
                                ) : (
                                  <>
                                    <div className="mt-1 text-2xl font-bold text-teal-900">
                                      {notaire.nbSalaries}
                                    </div>
                                    {notaire.notairesSalaries && (
                                      <div className="mt-2 text-sm text-teal-600">
                                        {notaire.notairesSalaries}
                                      </div>
                                    )}
                                  </>
                                )}
                              </div>
                            </div>
                          </div>

                          <div>
                            <h3 className="text-lg font-medium text-gray-900 mb-4">
                              Services
                            </h3>
                            <div className="bg-gray-50 p-4 rounded-lg">
                              <label className="flex items-center space-x-3">
                                {isEditing ? (
                                  <input
                                    type="checkbox"
                                    checked={editedNotaire.serviceNego}
                                    onChange={(e) => handleCheckboxChange('serviceNego', e.target.checked)}
                                    className="h-4 w-4 text-teal-600 focus:ring-teal-500 border-gray-300 rounded"
                                  />
                                ) : (
                                  <div className={`w-3 h-3 rounded-full ${
                                    notaire.serviceNego
                                      ? 'bg-green-500'
                                      : 'bg-gray-300'
                                  }`} />
                                )}
                                <span className="text-gray-900">
                                  Service négociation
                                </span>
                              </label>
                            </div>
                          </div>
                        </div>

                        <div>
                          <h3 className="text-lg font-medium text-gray-900 mb-4">
                            Notes
                          </h3>
                          <textarea
                            name="notes"
                            value={editedNotaire.notes || ''}
                            onChange={handleInputChange}
                            placeholder="Ajouter des notes..."
                            className="w-full h-64 rounded-lg border-gray-300 shadow-sm focus:border-teal-500 focus:ring-teal-500"
                            disabled={!isEditing}
                          />
                        </div>
                      </div>
                    ) : (
                      <ContactHistory
                        notaire={notaire}
                        onUpdate={onSave}
                      />
                    )}

                    {geocodingStatus && (
                      <div className={`mt-4 p-2 rounded ${
                        geocodingStatus.includes('Erreur') 
                          ? 'bg-red-100 text-red-700' 
                          : 'bg-green-100 text-green-700'
                      }`}>
                        {geocodingStatus}
                      </div>
                    )}

                    {isEditing && (
                      <div className="mt-6 flex justify-end space-x-3">
                        <button
                          onClick={handleCancel}
                          className="px-4 py-2 text-sm font-medium rounded-lg border border-gray-300 text-gray-700 hover:bg-gray-50 transition-colors duration-200"
                        >
                          Annuler
                        </button>
                        <button
                          onClick={handleSave}
                          className="px-4 py-2 text-sm font-medium rounded-lg bg-teal-600 text-white hover:bg-teal-700 transition-colors duration-200"
                        >
                          Enregistrer
                        </button>
                      </div>
                    )}
                  </div>
                </div>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition.Root>
  );
};

export default NotaireModal; 