import { StoredData, Filtres, VilleInteret } from '../types';

const STORAGE_KEY = 'notaires_app_data';

const defaultFiltres: Filtres = {
  villesInteret: [],
  typeNotaire: 'tous',
  serviceNego: 'tous',
  showOnlyInRadius: false,
  minAssocies: 0,
  maxAssocies: 999,
  minSalaries: 0,
  maxSalaries: 999,
  statuts: []  // Tableau vide pour aucun filtre de statut
};

export const storageService = {
  saveData: (data: StoredData) => {
    try {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(data));
    } catch (error) {
      console.error('Erreur lors de la sauvegarde des données:', error);
    }
  },

  loadData: (): StoredData => {
    try {
      const storedData = localStorage.getItem(STORAGE_KEY);
      if (storedData) {
        return JSON.parse(storedData);
      }
    } catch (error) {
      console.error('Erreur lors du chargement des données:', error);
    }
    return {
      villesInteret: [],
      filtres: defaultFiltres
    };
  },

  saveFiltres: (filtres: Filtres) => {
    try {
      const data = storageService.loadData();
      data.filtres = filtres;
      storageService.saveData(data);
    } catch (error) {
      console.error('Erreur lors de la sauvegarde des filtres:', error);
    }
  },

  saveVillesInteret: (villes: VilleInteret[]) => {
    try {
      const data = storageService.loadData();
      data.villesInteret = villes;
      storageService.saveData(data);
    } catch (error) {
      console.error('Erreur lors de la sauvegarde des villes d\'intérêt:', error);
    }
  },

  getDefaultFiltres: () => ({...defaultFiltres})
}; 