import React, { useState, useEffect, useRef } from 'react';
import { Filtres, VilleInteret, NotaireStatut } from '../types';
import { geocodeAddress } from '../services/geocoding';
import { googleSheetsService } from '../services/googleSheets';

interface Props {
  filtres: Filtres;
  onFiltresChange: (filtres: Filtres) => void;
  notairesCount: number;
}

interface VilleSuggestion {
  nom: string;
  codePostal: string;
  departement: string;
  population: number;
}

const CustomSelect: React.FC<{
  label: string;
  value: string;
  onChange: (value: string) => void;
  options: { value: string; label: string; icon?: string }[];
}> = ({ label, value, onChange, options }) => {
  const [isOpen, setIsOpen] = useState(false);
  const selectRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (selectRef.current && !selectRef.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  const selectedOption = options.find(opt => opt.value === value);

  return (
    <div className="relative" ref={selectRef}>
      <label className="block text-sm font-medium text-gray-700 mb-1">
        {label}
      </label>
      <button
        type="button"
        onClick={() => setIsOpen(!isOpen)}
        className="w-full bg-white border border-gray-300 rounded-lg px-4 py-2.5 text-sm text-left text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-teal-500 focus:border-teal-500"
      >
        <div className="flex items-center justify-between">
          <div className="flex items-center">
            {selectedOption?.icon && (
              <span className="mr-2">{selectedOption.icon}</span>
            )}
            <span>{selectedOption?.label}</span>
          </div>
          <svg
            className={`w-5 h-5 text-gray-400 transition-transform duration-200 ${
              isOpen ? 'transform rotate-180' : ''
            }`}
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
          </svg>
        </div>
      </button>

      {isOpen && (
        <div className="absolute z-10 w-full mt-1 bg-white rounded-lg shadow-lg border border-gray-200">
          {options.map((option) => (
            <button
              key={option.value}
              onClick={() => {
                onChange(option.value);
                setIsOpen(false);
              }}
              className={`w-full text-left px-4 py-2.5 text-sm hover:bg-gray-50 flex items-center ${
                option.value === value ? 'text-teal-600 bg-teal-50' : 'text-gray-700'
              }`}
            >
              {option.icon && <span className="mr-2">{option.icon}</span>}
              {option.label}
            </button>
          ))}
        </div>
      )}
    </div>
  );
};

const FiltersPanel: React.FC<Props> = ({ filtres, onFiltresChange, notairesCount }) => {
  const [nouvelleVille, setNouvelleVille] = useState('');
  const [nouveauRayon, setNouveauRayon] = useState(10);
  const [suggestions, setSuggestions] = useState<VilleSuggestion[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const [filtresAvances, setFiltresAvances] = useState(false);

  // Charger les villes d'intérêt sauvegardées
  useEffect(() => {
    const loadVillesInteret = async () => {
      try {
        const data = await googleSheetsService.loadFromSheet();
        if (data.villesInteret) {
          onFiltresChange({
            ...filtres,
            villesInteret: data.villesInteret
          });
        }
      } catch (error) {
        console.error('Erreur lors du chargement des villes d\'intérêt:', error);
      }
    };

    loadVillesInteret();
  }, []);

  // Sauvegarder les villes d'intérêt
  const saveVillesInteret = async (newVillesInteret: VilleInteret[]) => {
    try {
      await googleSheetsService.saveVillesInteret(newVillesInteret);
    } catch (error) {
      console.error('Erreur lors de la sauvegarde des villes d\'intérêt:', error);
    }
  };

  useEffect(() => {
    const fetchSuggestions = async () => {
      if (nouvelleVille.length < 2) {
        setSuggestions([]);
        return;
      }

      setIsLoading(true);
      try {
        const response = await fetch(
          `https://geo.api.gouv.fr/communes?nom=${encodeURIComponent(nouvelleVille)}&fields=nom,code,departement,population&boost=population&limit=5`
        );
        const data = await response.json();
        setSuggestions(
          data.map((ville: any) => ({
            nom: ville.nom,
            codePostal: ville.code,
            departement: ville.departement,
            population: ville.population
          }))
        );
      } catch (error) {
        console.error('Erreur lors de la recherche de villes:', error);
        setSuggestions([]);
      } finally {
        setIsLoading(false);
      }
    };

    const timeoutId = setTimeout(fetchSuggestions, 300);
    return () => clearTimeout(timeoutId);
  }, [nouvelleVille]);

  const handleSelectVille = async (suggestion: VilleSuggestion) => {
    try {
      const coords = await geocodeAddress(`${suggestion.nom} ${suggestion.codePostal}`);
      if (coords && !coords.error) {
        const nouvelleVilleInteret: VilleInteret = {
          id: Math.random().toString(36).substr(2, 9),
          nom: suggestion.nom,
          rayon: nouveauRayon,
          latitude: coords.lat,
          longitude: coords.lon,
          departement: suggestion.departement,
          population: suggestion.population
        };

        const newVillesInteret = [...filtres.villesInteret, nouvelleVilleInteret];
        onFiltresChange({
          ...filtres,
          villesInteret: newVillesInteret
        });
        
        // Sauvegarder les villes d'intérêt
        await saveVillesInteret(newVillesInteret);

        setNouvelleVille('');
        setNouveauRayon(10);
        setSuggestions([]);
        setShowSuggestions(false);
      }
    } catch (error) {
      console.error('Erreur lors du géocodage:', error);
    }
  };

  const handleRemoveVille = async (villeId: string) => {
    const newVillesInteret = filtres.villesInteret.filter(v => v.id !== villeId);
    onFiltresChange({
      ...filtres,
      villesInteret: newVillesInteret
    });
    
    // Sauvegarder les villes d'intérêt
    await saveVillesInteret(newVillesInteret);
  };

  const handleTypeNotaireChange = (value: 'tous' | 'individuels' | 'groupes') => {
    onFiltresChange({
      ...filtres,
      typeNotaire: value
    });
  };

  const handleServiceNegoChange = (value: 'tous' | 'oui' | 'non') => {
    onFiltresChange({
      ...filtres,
      serviceNego: value
    });
  };

  const handleShowOnlyInRadiusChange = (checked: boolean) => {
    onFiltresChange({
      ...filtres,
      showOnlyInRadius: checked
    });
  };

  const toggleStatut = (statut: NotaireStatut) => {
    const newStatuts = filtres.statuts.includes(statut)
      ? filtres.statuts.filter(s => s !== statut)
      : [...filtres.statuts, statut];
    onFiltresChange({
      ...filtres,
      statuts: newStatuts
    });
  };

  return (
    <div className="space-y-6">
      {/* Filtres principaux */}
      <div className="space-y-4">
        <div className="bg-white rounded-xl shadow-sm p-4 space-y-4">
          <h3 className="text-sm font-medium text-gray-900">Statut</h3>
          <div className="grid grid-cols-2 gap-2">
            <button
              onClick={() => toggleStatut('favori')}
              className={`px-3 py-2 rounded-lg text-sm font-medium transition-colors duration-200 ${
                filtres.statuts.includes('favori')
                  ? 'bg-teal-100 text-teal-800 ring-2 ring-teal-600'
                  : 'bg-white text-gray-700 border border-gray-200 hover:bg-teal-50'
              }`}
            >
              ⭐ Favoris
            </button>
            <button
              onClick={() => toggleStatut('envisage')}
              className={`px-3 py-2 rounded-lg text-sm font-medium transition-colors duration-200 ${
                filtres.statuts.includes('envisage')
                  ? 'bg-teal-100 text-teal-800 ring-2 ring-teal-600'
                  : 'bg-white text-gray-700 border border-gray-200 hover:bg-teal-50'
              }`}
            >
              🤔 À envisager
            </button>
            <button
              onClick={() => toggleStatut('non_interesse')}
              className={`px-3 py-2 rounded-lg text-sm font-medium transition-colors duration-200 ${
                filtres.statuts.includes('non_interesse')
                  ? 'bg-gray-200 text-gray-800 ring-2 ring-gray-600'
                  : 'bg-white text-gray-700 border border-gray-200 hover:bg-gray-100'
              }`}
            >
              ❌ Non intéressé
            </button>
            <button
              onClick={() => onFiltresChange({ ...filtres, statuts: [] })}
              className={`px-3 py-2 rounded-lg text-sm font-medium transition-colors duration-200 ${
                filtres.statuts.length === 0
                  ? 'bg-teal-100 text-teal-800 ring-2 ring-teal-600'
                  : 'bg-white text-gray-700 border border-gray-200 hover:bg-teal-50'
              }`}
            >
              👥 Tous
            </button>
          </div>
        </div>

        <div className="bg-white rounded-xl shadow-sm p-4 space-y-4">
          <CustomSelect
            label="Type d'étude"
            value={filtres.typeNotaire}
            onChange={(value) => handleTypeNotaireChange(value as 'tous' | 'individuels' | 'groupes')}
            options={[
              { value: 'tous', label: 'Tous les types', icon: '👥' },
              { value: 'individuels', label: 'Étude individuelle', icon: '👤' },
              { value: 'groupes', label: 'Notaires associés', icon: '👥' }
            ]}
          />
        </div>

        <div className="bg-white rounded-xl shadow-sm p-4 space-y-4">
          <CustomSelect
            label="Service négociation"
            value={filtres.serviceNego}
            onChange={(value) => handleServiceNegoChange(value as 'tous' | 'oui' | 'non')}
            options={[
              { value: 'tous', label: 'Tous', icon: '🔄' },
              { value: 'oui', label: 'Oui', icon: '✅' },
              { value: 'non', label: 'Non', icon: '❌' }
            ]}
          />
        </div>
      </div>

      {/* Filtres avancés */}
      <div className="bg-white rounded-xl shadow-sm p-4 space-y-4">
        <div className="flex items-center justify-between">
          <h3 className="text-sm font-medium text-gray-900">Filtres avancés</h3>
          <button
            onClick={() => setFiltresAvances(!filtresAvances)}
            className="text-sm text-teal-600 hover:text-teal-800 font-medium"
          >
            {filtresAvances ? 'Masquer' : 'Afficher'}
          </button>
        </div>

        {filtresAvances && (
          <div className="space-y-4 pt-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Nombre d'associés
              </label>
              <div className="grid grid-cols-2 gap-2">
                <div>
                  <label className="text-xs text-gray-500">Min</label>
                  <input
                    type="number"
                    min="0"
                    value={filtres.minAssocies}
                    onChange={(e) => onFiltresChange({
                      ...filtres,
                      minAssocies: parseInt(e.target.value) || 0
                    })}
                    className="w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500"
                  />
                </div>
                <div>
                  <label className="text-xs text-gray-500">Max</label>
                  <input
                    type="number"
                    min="0"
                    value={filtres.maxAssocies}
                    onChange={(e) => onFiltresChange({
                      ...filtres,
                      maxAssocies: parseInt(e.target.value) || 999
                    })}
                    className="w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500"
                  />
                </div>
              </div>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Nombre de salariés
              </label>
              <div className="grid grid-cols-2 gap-2">
                <div>
                  <label className="text-xs text-gray-500">Min</label>
                  <input
                    type="number"
                    min="0"
                    value={filtres.minSalaries}
                    onChange={(e) => onFiltresChange({
                      ...filtres,
                      minSalaries: parseInt(e.target.value) || 0
                    })}
                    className="w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500"
                  />
                </div>
                <div>
                  <label className="text-xs text-gray-500">Max</label>
                  <input
                    type="number"
                    min="0"
                    value={filtres.maxSalaries}
                    onChange={(e) => onFiltresChange({
                      ...filtres,
                      maxSalaries: parseInt(e.target.value) || 999
                    })}
                    className="w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500"
                  />
                </div>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Villes d'intérêt */}
      <div className="bg-white rounded-xl shadow-sm p-4 space-y-4">
        <h3 className="text-sm font-medium text-gray-900">Villes d'intérêt</h3>
        <div className="relative">
          <div className="flex space-x-2">
            <div className="flex-1 relative">
              <input
                type="text"
                value={nouvelleVille}
                onChange={(e) => setNouvelleVille(e.target.value)}
                onFocus={() => setShowSuggestions(true)}
                placeholder="Nom de la ville"
                className="w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500"
              />
              {isLoading && (
                <div className="absolute right-3 top-2">
                  <div className="animate-spin rounded-full h-5 w-5 border-2 border-teal-500 border-t-transparent"></div>
                </div>
              )}
            </div>
            <div className="w-32">
              <div className="relative">
                <input
                  type="number"
                  value={nouveauRayon}
                  onChange={(e) => setNouveauRayon(Math.max(1, parseInt(e.target.value) || 1))}
                  min="1"
                  placeholder="Rayon"
                  className="w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500 pr-8"
                />
                <span className="absolute right-3 top-2 text-gray-500 text-sm">km</span>
              </div>
            </div>
          </div>

          {showSuggestions && suggestions.length > 0 && (
            <div className="absolute z-10 w-full mt-1 bg-white rounded-lg shadow-lg border border-gray-200">
              {suggestions.map((suggestion) => (
                <button
                  key={suggestion.codePostal}
                  onClick={() => handleSelectVille(suggestion)}
                  className="w-full text-left px-4 py-2 hover:bg-gray-50 focus:outline-none focus:bg-gray-50"
                >
                  <div className="font-medium">{suggestion.nom}</div>
                  <div className="text-sm text-gray-600">
                    {suggestion.codePostal} - {suggestion.departement}
                    {suggestion.population && ` (${suggestion.population.toLocaleString()} hab.)`}
                  </div>
                </button>
              ))}
            </div>
          )}
        </div>

        {filtres.villesInteret.length > 0 && (
          <div className="space-y-2 max-h-60 overflow-y-auto">
            {filtres.villesInteret.map((ville) => (
              <div
                key={ville.id}
                className="flex items-center justify-between bg-gray-50 p-3 rounded-lg group hover:bg-gray-100"
              >
                <div>
                  <div className="font-medium text-gray-900">{ville.nom}</div>
                  <div className="text-sm text-gray-600">
                    Rayon : {ville.rayon} km
                    {ville.population && ` • ${ville.population.toLocaleString()} hab.`}
                  </div>
                </div>
                <button
                  onClick={() => handleRemoveVille(ville.id)}
                  className="text-gray-400 hover:text-red-500 opacity-0 group-hover:opacity-100 transition-opacity"
                >
                  <svg className="w-5 h-5" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
                  </svg>
                </button>
              </div>
            ))}
          </div>
        )}

        <div className="flex items-center space-x-2 pt-4">
          <input
            type="checkbox"
            checked={filtres.showOnlyInRadius}
            onChange={(e) => handleShowOnlyInRadiusChange(e.target.checked)}
            className="h-4 w-4 text-teal-600 focus:ring-teal-500 border-gray-300 rounded"
          />
          <label className="text-sm text-gray-700">
            Uniquement dans les rayons
          </label>
        </div>
      </div>

      {/* Bouton de réinitialisation */}
      <button
        onClick={() => onFiltresChange({
          villesInteret: [],
          typeNotaire: 'tous',
          serviceNego: 'tous',
          showOnlyInRadius: false,
          minAssocies: 0,
          maxAssocies: 999,
          minSalaries: 0,
          maxSalaries: 999,
          statuts: []
        })}
        className="w-full bg-gray-100 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-200 transition-colors duration-200"
      >
        Réinitialiser les filtres
      </button>
    </div>
  );
};

export default FiltersPanel; 