import React, { useState } from 'react';
import { Notaire, NotaireStatut } from '../types';

interface Props {
  notaires: Notaire[];
}

interface StatCard {
  title: string;
  value: number | string;
  description?: string;
  color: string;
}

const Dashboard: React.FC<Props> = ({ notaires }) => {
  const [isCollapsed, setIsCollapsed] = useState(false);

  // Calcul des statistiques
  const stats: StatCard[] = [
    {
      title: 'Total Notaires',
      value: notaires.length,
      description: 'Nombre total d\'études',
      color: 'teal'
    },
    {
      title: 'Favoris',
      value: notaires.filter(n => n.statut === 'favori').length,
      description: 'Études favorites',
      color: 'yellow'
    },
    {
      title: 'À envisager',
      value: notaires.filter(n => n.statut === 'envisage').length,
      description: 'Études à étudier',
      color: 'blue'
    },
    {
      title: 'Contactés',
      value: notaires.filter(n => n.contacts.length > 0).length,
      description: 'Études déjà contactées',
      color: 'purple'
    }
  ];

  const contactStats = {
    total: notaires.filter(n => n.contacts.length > 0).length,
    reponses: notaires.filter(n => n.contacts.some(c => c.reponseRecue)).length,
    positives: notaires.filter(n => n.contacts.some(c => c.reponseRecue?.positive)).length
  };

  const tauxReponse = contactStats.total > 0
    ? Math.round((contactStats.reponses / contactStats.total) * 100)
    : 0;

  const prochaines = notaires
    .filter(n => n.contacts.length > 0)
    .sort((a, b) => {
      const dateA = new Date(a.contacts[a.contacts.length - 1].date);
      const dateB = new Date(b.contacts[b.contacts.length - 1].date);
      return dateB.getTime() - dateA.getTime();
    })
    .slice(0, 5);

  if (isCollapsed) {
    return (
      <button
        onClick={() => setIsCollapsed(false)}
        className="w-full bg-white rounded-xl shadow-sm p-4 text-left hover:bg-gray-50 transition-colors duration-200"
      >
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-2">
            <svg className="w-5 h-5 text-teal-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
            </svg>
            <span className="font-medium text-gray-900">Afficher le tableau de bord</span>
          </div>
          <div className="text-sm text-gray-500">
            {notaires.length} notaires • {stats[1].value} favoris • {stats[2].value} à envisager
          </div>
        </div>
      </button>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h2 className="text-lg font-medium text-gray-900">Tableau de bord</h2>
        <button
          onClick={() => setIsCollapsed(true)}
          className="text-sm text-gray-500 hover:text-gray-700 flex items-center space-x-1"
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 15l7-7 7 7" />
          </svg>
          <span>Replier</span>
        </button>
      </div>

      {/* Cartes de statistiques */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        {stats.map((stat, index) => (
          <div key={index} className={`bg-${stat.color}-50 p-6 rounded-xl shadow-sm`}>
            <div className={`text-${stat.color}-600 text-sm font-medium`}>
              {stat.title}
            </div>
            <div className={`mt-2 text-3xl font-bold text-${stat.color}-700`}>
              {stat.value}
            </div>
            {stat.description && (
              <div className={`mt-1 text-${stat.color}-600 text-sm`}>
                {stat.description}
              </div>
            )}
          </div>
        ))}
      </div>

      {/* Statistiques de contact */}
      <div className="bg-white rounded-xl shadow-sm p-6">
        <h3 className="text-lg font-medium text-gray-900 mb-4">
          Statistiques de contact
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div className="text-center">
            <div className="text-3xl font-bold text-teal-600">
              {contactStats.total}
            </div>
            <div className="text-sm text-gray-600">Études contactées</div>
          </div>
          <div className="text-center">
            <div className="text-3xl font-bold text-teal-600">
              {tauxReponse}%
            </div>
            <div className="text-sm text-gray-600">Taux de réponse</div>
          </div>
          <div className="text-center">
            <div className="text-3xl font-bold text-teal-600">
              {contactStats.positives}
            </div>
            <div className="text-sm text-gray-600">Réponses positives</div>
          </div>
        </div>
      </div>

      {/* Derniers contacts */}
      <div className="bg-white rounded-xl shadow-sm p-6">
        <h3 className="text-lg font-medium text-gray-900 mb-4">
          Derniers contacts
        </h3>
        <div className="space-y-4">
          {prochaines.map((notaire) => {
            const dernierContact = notaire.contacts[notaire.contacts.length - 1];
            return (
              <div key={notaire.id} className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div>
                  <div className="font-medium text-gray-900">
                    {notaire.officeNotarial}
                  </div>
                  <div className="text-sm text-gray-600">
                    {new Date(dernierContact.date).toLocaleDateString()} - {dernierContact.type}
                  </div>
                </div>
                <div className={`px-3 py-1 rounded-full text-sm font-medium ${
                  dernierContact.reponseRecue
                    ? dernierContact.reponseRecue.positive
                      ? 'bg-green-100 text-green-800'
                      : 'bg-red-100 text-red-800'
                    : 'bg-yellow-100 text-yellow-800'
                }`}>
                  {dernierContact.reponseRecue
                    ? dernierContact.reponseRecue.positive
                      ? 'Réponse positive'
                      : 'Réponse négative'
                    : 'En attente'}
                </div>
              </div>
            );
          })}
          {prochaines.length === 0 && (
            <div className="text-center text-gray-500 py-4">
              Aucun contact récent
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Dashboard; 