import React, { useState } from 'react';
import { Notaire, Contact, ContactStatut } from '../types';

interface Props {
  notaire: Notaire;
  onUpdate: (notaire: Notaire) => void;
}

const ContactHistory: React.FC<Props> = ({ notaire, onUpdate }) => {
  const [newContact, setNewContact] = useState<Partial<Contact>>({
    type: 'initial',
    par: 'Fanny',
    statut: 'mail_envoye'
  });

  const handleAddContact = () => {
    if (!newContact.date) return;

    const contact: Contact = {
      date: newContact.date,
      type: newContact.type || 'initial',
      par: newContact.par || 'Fanny',
      statut: newContact.statut || 'mail_envoye'
    };

    onUpdate({
      ...notaire,
      contacts: [...notaire.contacts, contact],
      dateModification: new Date().toISOString()
    });

    setNewContact({
      type: 'initial',
      par: 'Fanny',
      statut: 'mail_envoye'
    });
  };

  const handleUpdateContact = (index: number, updates: Partial<Contact>) => {
    const updatedContacts = [...notaire.contacts];
    updatedContacts[index] = { ...updatedContacts[index], ...updates };

    onUpdate({
      ...notaire,
      contacts: updatedContacts,
      dateModification: new Date().toISOString()
    });
  };

  return (
    <div className="space-y-6">
      {/* Formulaire d'ajout de contact */}
      <div className="bg-white rounded-xl shadow-sm p-6">
        <h3 className="text-lg font-medium text-gray-900 mb-4">
          Nouveau contact
        </h3>
        <div className="grid grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Date
            </label>
            <input
              type="date"
              value={newContact.date || ''}
              onChange={(e) => setNewContact({ ...newContact, date: e.target.value })}
              className="w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Type
            </label>
            <select
              value={newContact.type}
              onChange={(e) => setNewContact({ ...newContact, type: e.target.value as 'initial' | 'relance' })}
              className="w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500"
            >
              <option value="initial">Premier contact</option>
              <option value="relance">Relance</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Par
            </label>
            <select
              value={newContact.par}
              onChange={(e) => setNewContact({ ...newContact, par: e.target.value as 'Fanny' | 'Jade' })}
              className="w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500"
            >
              <option value="Fanny">Fanny</option>
              <option value="Jade">Jade</option>
            </select>
          </div>
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Statut
            </label>
            <select
              value={newContact.statut}
              onChange={(e) => setNewContact({ ...newContact, statut: e.target.value as ContactStatut })}
              className="w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500"
            >
              <option value="mail_envoye">Mail envoyé</option>
              <option value="relance_envoyee">Relance envoyée</option>
              <option value="reponse_recue">Réponse reçue</option>
              <option value="cloture">Clôturé</option>
            </select>
          </div>
        </div>
        <div className="mt-4">
          <button
            onClick={handleAddContact}
            disabled={!newContact.date}
            className="w-full bg-teal-600 text-white px-4 py-2 rounded-lg hover:bg-teal-700 disabled:opacity-50 disabled:cursor-not-allowed"
          >
            Ajouter le contact
          </button>
        </div>
      </div>

      {/* Historique des contacts */}
      <div className="bg-white rounded-xl shadow-sm p-6">
        <h3 className="text-lg font-medium text-gray-900 mb-4">
          Historique des contacts
        </h3>
        <div className="space-y-4">
          {notaire.contacts.map((contact, index) => (
            <div key={index} className="bg-gray-50 rounded-lg p-4">
              <div className="flex items-center justify-between mb-2">
                <div className="font-medium text-gray-900">
                  {new Date(contact.date).toLocaleDateString()}
                </div>
                <div className="text-sm text-gray-600">
                  Par {contact.par}
                </div>
              </div>
              <div className="flex items-center space-x-2 mb-2">
                <span className="text-sm text-gray-600">
                  {contact.type === 'initial' ? 'Premier contact' : 'Relance'}
                </span>
                <span className="text-gray-300">•</span>
                <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                  contact.statut === 'reponse_recue'
                    ? 'bg-green-100 text-green-800'
                    : contact.statut === 'cloture'
                    ? 'bg-red-100 text-red-800'
                    : 'bg-yellow-100 text-yellow-800'
                }`}>
                  {contact.statut === 'mail_envoye' ? 'Mail envoyé'
                    : contact.statut === 'relance_envoyee' ? 'Relance envoyée'
                    : contact.statut === 'reponse_recue' ? 'Réponse reçue'
                    : 'Clôturé'}
                </span>
              </div>
              {contact.statut === 'reponse_recue' && (
                <div className="mt-2">
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Réponse
                  </label>
                  <div className="flex items-center space-x-4">
                    <label className="inline-flex items-center">
                      <input
                        type="radio"
                        checked={contact.reponseRecue?.positive}
                        onChange={() => handleUpdateContact(index, {
                          reponseRecue: { date: new Date().toISOString(), positive: true }
                        })}
                        className="text-teal-600 focus:ring-teal-500"
                      />
                      <span className="ml-2 text-sm text-gray-700">Positive</span>
                    </label>
                    <label className="inline-flex items-center">
                      <input
                        type="radio"
                        checked={contact.reponseRecue?.positive === false}
                        onChange={() => handleUpdateContact(index, {
                          reponseRecue: { date: new Date().toISOString(), positive: false }
                        })}
                        className="text-teal-600 focus:ring-teal-500"
                      />
                      <span className="ml-2 text-sm text-gray-700">Négative</span>
                    </label>
                  </div>
                  {contact.reponseRecue && (
                    <textarea
                      value={contact.reponseRecue.commentaire || ''}
                      onChange={(e) => handleUpdateContact(index, {
                        reponseRecue: { ...contact.reponseRecue!, commentaire: e.target.value }
                      })}
                      placeholder="Commentaire sur la réponse..."
                      className="mt-2 w-full border-gray-300 rounded-lg shadow-sm focus:border-teal-500 focus:ring-1 focus:ring-teal-500"
                      rows={3}
                    />
                  )}
                </div>
              )}
            </div>
          ))}
          {notaire.contacts.length === 0 && (
            <div className="text-center text-gray-500 py-4">
              Aucun contact enregistré
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default ContactHistory; 